html {
	font-size: 10px;
}

*,
*:before,
*:after {
	box-sizing: border-box;
}

body {
	margin: 0;
	min-width: 320px;
	background: white;
	color: #001e41;
	font: 400 1.4rem/1.5 "Roboto", sans-serif;
	font-smoothing: antialiased;
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	display: flex;
	flex-direction: column;
}
body.overflow-disabled, body.menu-opened {
	overflow: hidden;
}

a {
	text-decoration: none;
	color: inherit;
	transition: all 0.3s ease;
}

a:hover {
	text-decoration: underline;
}

input {
	outline: none;
}

button,
input[type=search],
input[type=button],
input[type=reset],
input[type=submit] {
	-webkit-appearance: none;
	cursor: pointer;
	transition: all 0.3s ease;
}

button::-moz-focus-inner,
input[type=reset]::-moz-focus-inner,
input[type=button]::-moz-focus-inner,
input[type=submit]::-moz-focus-inner {
	border: none;
	padding: 0;
}

.wrapper {
	min-height: 100vh;
	min-height: calc(var(--vh, 1vh) * 100);
	display: flex;
	flex-direction: column;
}

#main {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
}

p {
	margin-top: 0;
}

h1,
h2,
h3,
h4,
h5 {
	margin-top: 0;
}

.container {
	max-width: 127.5rem;
	margin: auto;
	padding-left: 1rem;
	padding-right: 1rem;
}

.sr-only {
	position: absolute !important;
	width: 1px !important;
	height: 1px !important;
	padding: 0 !important;
	margin: -1px !important;
	overflow: hidden !important;
	clip: rect(0, 0, 0, 0) !important;
	white-space: nowrap !important;
	border: 0 !important;
}

.home-page {
	padding-top: 11.7rem;
}
@media (max-width: 767px) {
	.home-page {
		padding-top: 5.9rem;
	}
}

.modal-overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding: 3rem;
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 100;
	pointer-events: none;
}
.modal-overlay .modal {
	width: 100%;
	background: #ffffff;
	box-shadow: 0 1rem 2rem rgba(93, 120, 255, 0.05);
	border-radius: 1.5rem;
	pointer-events: none;
	visibility: hidden;
	opacity: 0;
	transition: all 0.3s ease;
}
@media (max-width: 767px) {
	.modal-overlay .modal {
		border-radius: 1.125rem;
	}
}
.modal-overlay .modal .modal-body {
	position: relative;
}
.modal-overlay .modal .btn-close {
	position: absolute;
	top: 1rem;
	right: 1rem;
	width: 3rem;
	height: 3rem;
	background-color: #f3f4f6;
	background-image: url("data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 12 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.1875 11.6875L0.3125 10.8125L5.125 6L0.3125 1.1875L1.1875 0.3125L6 5.125L10.8125 0.3125L11.6875 1.1875L6.875 6L11.6875 10.8125L10.8125 11.6875L6 6.875L1.1875 11.6875Z' fill='%23001E41'/%3E%3C/svg%3E%0A");
	background-repeat: no-repeat;
	background-position: center;
	border-radius: 50%;
	border: none;
	z-index: 2;
	cursor: pointer;
}
@media (max-width: 767px) {
	.modal-overlay .modal .btn-close {
		width: 2.2rem;
		height: 2.2rem;
		background-size: 1rem;
	}
}
.modal-overlay .modal .btn-close:hover {
	background-color: rgb(228.4285714286, 230.6428571429, 235.0714285714);
}
.modal-overlay .modal.show {
	opacity: 1;
	visibility: visible;
	pointer-events: all;
}

/* header */
#header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	padding-top: 1.8rem;
	padding-bottom: 1.8rem;
	z-index: 100;
	background: white;
	border-bottom: 1px solid #e5e5e5;
}
@media (max-width: 767px) {
	#header {
		padding-top: 1.4rem;
		padding-bottom: 1.4rem;
	}
}
#header .logo {
	display: inline-flex;
	width: 100%;
	max-width: 14rem;
}
@media (min-width: 992px) {
	#header .logo {
		margin-right: 6.1rem;
	}
}
@media (max-width: 767px) {
	#header .logo {
		max-width: 8.9rem;
	}
}
#header .logo img {
	width: 100%;
	height: auto;
}
#header .row {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
#header .row .col:first-child {
	display: flex;
}
#header .row .col:last-child {
	display: flex;
	flex: 1;
	align-items: center;
}
@media (max-width: 991px) {
	#header .row .col:last-child {
		justify-content: flex-end;
	}
}
#header .nav {
	flex: 1;
	transition: all 0.3s ease;
}
@media (max-width: 991px) {
	#header .nav {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		background: white;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
}
@media (min-width: 992px) {
	#header .nav .inner-holder {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
}
@media (max-width: 991px) {
	#header .nav .inner-holder {
		overflow: auto;
		width: 100%;
		padding: 3rem 0;
	}
}
#header .nav .menu {
	margin: 0;
	padding: 0;
	list-style: none;
	font-weight: 500;
	font-size: 1.8rem;
	color: #7d7d7d;
}
@media (min-width: 992px) {
	#header .nav .menu {
		display: flex;
		flex-wrap: wrap;
		margin: auto;
	}
}
@media (max-width: 991px) {
	#header .nav .menu {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}
@media (min-width: 992px) {
	#header .nav .menu .menu-item:not(:last-child) {
		margin-right: 3rem;
	}
}
@media (max-width: 991px) {
	#header .nav .menu .menu-item {
		margin-bottom: 3rem;
	}
}
@media (max-width: 991px) {
	.menu-opened #header .nav {
		opacity: 1;
		visibility: visible;
		pointer-events: all;
	}
}
#header .btns-holder {
	display: flex;
}
@media (max-width: 991px) {
	#header .btns-holder {
		flex-direction: column;
		align-items: center;
	}
}
@media (min-width: 992px) {
	#header .btns-holder .btn:first-child {
		margin-right: 2.2rem;
	}
}
@media (max-width: 991px) {
	#header .btns-holder .btn:first-child {
		margin-bottom: 1rem;
	}
}
#header .open-menu {
	position: relative;
	width: 2.2rem;
	height: 2.2rem;
	display: block;
	font-size: 0;
	line-height: 0;
}
@media (min-width: 992px) {
	#header .open-menu {
		display: none;
	}
}
#header .open-menu span {
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
	background: #001e41;
	border-radius: 0.2rem;
	height: 0.2rem;
	transition: all 0.3s ease;
}
#header .open-menu span:nth-child(1) {
	margin-top: -0.7rem;
}
#header .open-menu span:nth-child(2) {
	margin-top: -0.2rem;
}
#header .open-menu span:nth-child(3) {
	margin-top: 0.3rem;
}
.menu-opened #header .open-menu span:nth-child(1) {
	margin-top: 0;
	transform: rotate(45deg);
}
.menu-opened #header .open-menu span:nth-child(2) {
	opacity: 0;
	transform: translateX(50%);
}
.menu-opened #header .open-menu span:nth-child(3) {
	margin-top: 0;
	transform: rotate(-45deg);
}

.btn {
	display: inline-flex;
	justify-content: center;
	font-weight: 400;
	font-size: 1.8rem;
	line-height: 1;
	padding: 1.2rem 1.9rem;
}
@media (max-width: 767px) {
	.btn {
		padding: 1.2rem 3rem;
		font-size: 1.4rem;
	}
}
.btn-link {
	color: #5d78ff;
}
.btn-primary {
	color: #ffffff;
	background: #5d78ff;
	border-radius: 0.8rem;
	border: none;
}
.btn-primary:hover {
	background: rgb(118.5, 141.25, 255);
	text-decoration: none;
}
.btn:disabled, .btn[disabled] {
	background: #aed1ff;
	pointer-events: none;
}

@media (min-width: 768px) {
	.intro-section .row {
		display: flex;
		justify-content: space-between;
	}
}
